import Vue from 'vue'
//vuex
import Vuex from 'vuex'
//state.js
import state from './state' 
import getters from './getters'
import mutations from './mutations'
import actions from './actions'
Vue.use(Vuex)
//vuex  store 
const store = new Vuex.Store({
    //  data
    state,
    // getterscomputed
    // state
    getters,
    // methods
    // mutations  actions
    mutations,
  // Action  mutation
  // Action  mutation
  // Action 
    actions
  })


  //storemain.js
  export default store;


